/* src/components/blocInfo.css */
.bloc-info {
    margin: 0;
    padding: 10px 0;
    background: #f7f7f7;
    box-shadow: 0 5px 40px -7px rgba(0, 0, 0, 0.08);
}

.social-links {
    margin: 0;
    padding: 10px 0;
    background: #f7f7f7;
    box-shadow: 0 5px 40px -7px rgba(0, 0, 0, 0.08);
}

.social-links a {
    color: #0078d7;
    margin: 0 10px;
    font-size: 1.5em;
    transition: color 0.3s;
}

.social-links a:hover {
    color: #005bb5;
}

.social-links div {
    margin: 10px 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.qr-code-container {
    margin: 0;
    padding: 0;
    text-align: center;
    padding: 10px 0;
    background: #f7f7f7;
    font-weight: bold;
    box-shadow: 0 5px 40px -7px rgba(0, 0, 0, 0.08);
}

.qr-code-container h4 {
    margin-bottom: 10px;
    font-size: 14px;
    color: #333;
}

.bloc-info div {
    display: flex;
    align-items: center;
    justify-content: center;
}

.bloc-info a {
    color: #007bff;
    text-decoration: none;
}

.vcard-row {

    position: relative;
    padding: 20px 0 20px 65px;
    float: left;
    width: 100%;
}

.separator {
    height: 1px;
    background-color: #eaeaea;
    float: right;
    width: calc(100% - 60px);
}
