@media (max-width: 768px) {
    .vcard-header-wrapper {
        background-color: #0288d1;
        padding: 0px;
    }

    .profile-header {
        background-color: #0078d7;
        color: white;
        box-shadow: 0 -5px 40px 7px rgba(0, 0, 0, 0.08);
        text-align: center;
    }

    .vcard-buttons {
        display: block;
        width: 100%;
        text-align: center;
        height: 60px;
        border-top: 1px solid rgba(255, 255, 255, 0.15);
    }

    .profile-pic {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        margin-bottom: 10px;
        border: 3px solid white;
        margin-top: 60px;
    }

    .action-buttons button {
        background: transparent;
        display: table-cell;
        border: none;
        cursor: pointer;
        color: white;
        padding: 12px 0;
        cursor: pointer;
        min-width: 20%;
        font-size: 0.9em;
        border-right: 1px solid rgba(255, 255, 255, 0.15);
    }

    .action-buttons button:hover {
        text-decoration: none;
        background-color: #0056d7;
    }

    .vcard-functions a {
        cursor: pointer;
        display: table-cell;
        color: #fff;
        min-width: 100px;
        margin: 0;
        border-right: 1px solid rgba(255, 255, 255, 0.15);
    }

}

