.download-button {
    margin-top: 20px;
    padding: 10px;
    width: 100%;
    border: none;
    color: white;
    background-color: #007bff;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    margin: 0;
    padding: 10px 0;
    box-shadow: 0 5px 40px -7px rgba(0, 0, 0, 0.08);
}