body {
  font-family: 'Maison Neue', sans-serif, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f7f7f7;
  line-height: 1;
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (max-width: 768px) {
  h1 {
    display: block;
    font-size: 18px;
    margin-block-start: 0.83em;
    margin-block-end: 0.83em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
    unicode-bidi: isolate;
  }

  h2 {
    font-size: 14px;
    color: #ddeeff;
    display: block;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: normal;
    unicode-bidi: isolate;
  }

  p {
    font-size: 15px;
    margin: 0 60px 0 60px;
    line-height: 1.3;
    color: #323032;
    font-weight: 400;
    padding-top: 35px;
    padding-bottom: 15px;
  }

  small {
    text-transform: uppercase;
    line-height: 1.3;
    font-size: 10px;
  }

  span {
    line-height: 1.3;
    font-size: 15px;
    margin-left: 10px;
  }

  .business-card {
    box-shadow: 0 -5px 40px 7px rgba(0, 0, 0, 0.08);
    margin: 0;
    padding: 0;
  }
}

@media (min-width: 769px) {

  h1 {
    display: block;
    font-size: 18px;
    margin-block-start: 0.83em;
    margin-block-end: 0.83em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
    unicode-bidi: isolate;
  }

  h2 {
    font-size: 14px;
    color: #ddeeff;
    display: block;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: normal;
    unicode-bidi: isolate;
  }

  p {
    font-size: 15px;
    margin: 0 60px 0 60px;
    line-height: 1.3;
    color: #323032;
    font-weight: 400;
    padding-top: 35px;
    padding-bottom: 15px;
  }

  small {
    text-transform: uppercase;
    line-height: 1.3;
    font-size: 10px;

  }

  span {
    line-height: 1.3;
    font-size: 15px;
    margin-left: 10px;
  }

  .business-card {
    margin: 0 auto;
    width: 570px;

    padding-top: 60px;
  }
}